.leftbox{
    width: 30vw;
}

.leftbox > div{
    width: 90%;
margin-left: 5%;
border-radius: 20px;
background-color: #fff;
margin-top: 20px;
/* box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.14); */
/* padding: 22px; */
margin-top: 20px;
display: block;
position: relative;
cursor: pointer;
border-radius: 13px;
padding: 25px;
border: 1px solid #ddd;
/* width: 80%; */
margin-left: 5%;
}

.tax-full input, .tax-full textarea, .leftbox input, .leftbox textarea, .leftbox .select-tags .rbt .rbt-input-wrapper{
    width: 100%;
    margin-bottom: 14px;
    background: #fff;
    border: 1px solid #c8c8c8;
    font-size: 15px;
    padding: 10px;
    border-radius: 10px;
    resize: none;
    color: #242424;
    font-family: "FiraGO", sans-serif;
}

.tax-full textarea{
    min-height: 90px;
}
.tab {
    font-size: 13px;
    display: inline-block;
    padding: 5px;
    cursor: pointer;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    margin-left: 2px;
    background: #efefef;
    margin-bottom: 5px;
}

.leftbox h3{
    margin-top: 30px;
    font-size: 18px;
}

.leftbox > button, .tax-full button, .round-btn-transp {
    background: #4670be;
border: none;
/* margin-left: 55px; */
margin-top: 20px;
color: #fff;
padding: 10px 20px;
border-radius: 30px;
}

.leftbox .sugg{
    font-size: 13px;
    margin-top: 11px;
}

.leftbox thead {
    color: gray;
    font-size: 13px;
    
}

.leftbox thead td{ padding-bottom: 7px;}


.tax-full{
    /* background: #e2e2e2; */
    width: 100%;
}

.tax-title-line{
    width: 100%;
height: 61px;
background: #fff;
border-bottom: 2px solid #d0d0d0;
}

.tax-mid{
    width: 70%;
    margin: 0 auto;
}

.tax-title-line > .tax-mid{
    display: flex;
    align-items:center;
    height: 100%;
    font-size: 19px;
}

input[type="radio"], input[type="checkbox"]{
    width:15px;
    min-height: 6px;
    height:14px;
    -webkit-appearance: radio;
    appearance: radio;
}

.tax-mid input[type="date"]{
    width: 37%;
margin-right: 5.5%;
}

.fa-facebook{
    color: #4064ac;
}

.fa-twitter{
    color: #1c9cea
}

.fa-youtube{
    color: #f70000
}

.fa-trash-can{
    color:rgba(0, 0, 0, 0.14)
}

.account{
    width: 70%;
}

.account .fa-facebook, .account .fa-twitter, .account .fa-youtube {
    width: 18px
}

.account .fa-trash-can{
    float: right
}

.upload-btn{
    cursor: pointer
}

.left-m-5{
    margin-left: 5%;
}


.leftbox td:first-child{
    width: 100%;
}